import {api} from '/js/api';
import {Languages, Store} from '/js/util';


let act = Store.get('RoleName') === 'exhibition' ? 'exhibition' : 'buyer';

api({
	exhibition_result: 'exhibition/ExhibitionWishListResult',
	buyer_result: 'buyer/BuyerWishListResult',
});


api[`${act}_result`](res => {
	res.percent = (res.selected / res.max * 100).toFixed(0) + '%';
	let data = {
		result: res,
		resultTitle: Languages.package.resultTitle,
		selected: Languages.package.selected,
		atLeast: Languages.package.atLeast,
		atMost: Languages.package.atMost,
	};

	let mask = $(`<div class="mask" style="background-color: rgba(0,0,0,0.6)"></div>`);

	let htmlContent = template('tpl-result', data);

	mask.append(htmlContent);
	$('body').append(mask);

	mask.on('click', function () {
		history.back();
	});
});
